@font-face {
  font-family: "Nunitolight";
  src: url("../fonts/Nunito/Nunito-Light.woff2") format("woff2"), url("../fonts/Nunito/Nunito-Light.svg#Nunitolight") format("svg");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "Nunitoregular";
  src: url("../fonts/Nunito/Nunito-Regular.woff2") format("woff2"), url("../fonts/Nunito/Nunito-Regular.svg#Nunitoregular") format("svg");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Nunitobold";
  src: url("../fonts/Nunito/Nunito-Bold.woff2") format("woff2"), url("../fonts/Nunito/Nunito-Bold.svg#Nunitobold") format("svg");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Nunitoblack";
  src: url("../fonts/Nunito/Nunito-Black.woff2") format("woff2"), url("../fonts/Nunito/Nunito-Black.svg#Nunitoblack") format("svg");
  font-weight: 900;
  font-style: normal;
}
/** Media Queries **/
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.5;
}

p {
  font-size: 2rem;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  background-repeat: no-repeat;
}

h2,
h3 {
  text-align: center;
  margin: 0;
}

h1 {
  max-width: 70%;
  margin: 0 auto;
  text-align: center;
  font-size: 6.4rem;
  font-weight: "Nunitobold", sans-serif;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 3rem;
}

body {
  background-color: #f8f8f8;
  font-family: "Nunitoregular", sans-serif;
  color: #3B4252;
}

.container {
  width: min(90%, 120rem);
  margin: 0 auto;
}

.main {
  background-color: #88C0D0;
}

.nav {
  display: flex;
  justify-content: space-between;
  padding: 4rem;
  background-color: #f8f8f8;
}
.nav .nav__link {
  display: none;
  color: #3B4252;
  font-family: "Nunitoregular", sans-serif;
}

.static {
  overflow: hidden;
}

.nav--show {
  height: 100vh;
}
.nav--show .container {
  margin-top: 4rem;
  font-size: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5rem;
}
.nav--show .nav__link {
  display: inline-block;
}

@media (min-width: 1024px) {
  .nav {
    justify-content: flex-end;
  }
  .nav #menuIcon {
    display: none;
  }
  .nav .container {
    display: flex;
    justify-content: flex-end;
    gap: 2rem;
  }
  .nav .container .nav__link {
    display: inline-block;
    font-size: 2rem;
    padding: 0.5rem;
  }
  .nav .container .nav__link:hover {
    color: #88C0D0;
  }
}
.header {
  margin: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #88C0D0;
  padding: 1rem;
}
.header .about__profilePicture img {
  width: 25rem;
  border-radius: 50%;
}

@media (min-width: 1024px) {
  .header {
    display: flex;
    justify-content: space-between;
    max-width: 70%;
    margin: 7rem auto;
    padding: 2.5rem;
  }
  .header .about {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .header .about h2 {
    text-align: left;
    font-size: 2.8rem;
  }
  .header .about .about__description {
    flex: 2.5 0 0;
  }
  .header .about .about__profilePicture {
    flex: 1 0 0;
  }
  .header .tecnologias {
    display: flex;
    justify-content: space-between;
  }
  .header .tecnologias .tecnologias__using,
.header .tecnologias .tecnologias__learning {
    display: flex;
    font-family: "Nunitobold", sans-serif;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  .header .tecnologias .iconsContainer {
    display: flex;
    gap: 3rem;
  }
}
.portafolio {
  background-color: #ECEFF4;
}

.pages {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
.pages h2 {
  margin: 3rem 0;
}

.pages__target {
  background-color: #88C0D0;
  padding: 2.5rem;
  border-radius: 10px;
  margin-bottom: 5rem;
  text-align: center;
  box-shadow: 0px 10px 25px 0px rgba(0, 0, 0, 0.4);
  -webkit-box-shadow: 0px 8px 25px 0px rgba(0, 0, 0, 0.4);
  -moz-box-shadow: 0px 10px 25px 0px rgba(0, 0, 0, 0.4);
}
.pages__target:last-of-type {
  margin-bottom: 0;
}
.pages__target p {
  font-family: "Nunitobold", sans-serif;
  color: #fff;
  display: inline-block;
  margin: 1.5rem 0 2.5rem 0;
}

.pages__img {
  filter: grayscale(1);
  border-radius: 1.5rem;
}
.pages__img:hover {
  filter: saturate(1);
}

@media (min-width: 768px) {
  .pages {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, auto);
    place-items: center;
    gap: 5rem;
  }
  .pages p {
    font-size: 2.4rem;
  }
  .pages .pages__target {
    margin: 0;
  }

  .pages h2 {
    grid-column: 1/3;
  }
}
#servicios {
  display: flex;
  flex-direction: column;
  align-items: center;
}
#servicios h2 {
  padding: 0 0.3rem;
}

.servicios {
  margin-top: 3rem;
}

.servicios__info {
  text-align: center;
}
.servicios__info h3 {
  margin-top: 8rem;
}
.servicios__info img {
  max-height: 15rem;
}
.servicios__info:first-of-type h3 {
  margin-top: 3rem;
}

@media (min-width: 768px) {
  #servicios h2 {
    padding: 0.3rem 4rem;
  }

  .servicios {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }

  .servicios__info {
    display: flex;
    flex-direction: column;
  }
  .servicios__info img {
    max-height: 19%;
  }
  .servicios__info h3 {
    margin: 3rem 0;
    order: 1;
  }
  .servicios__info img {
    order: 0;
  }
  .servicios__info p {
    order: 2;
  }
  .servicios__info ul {
    list-style: square;
    order: 3;
  }
}
@media (min-width: 1024px) {
  .servicios__info img {
    max-height: 40%;
  }
}
.footer {
  background-color: #D8DEE9;
  padding: 2rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.footer p {
  text-align: center;
  padding: 0;
}

.dark {
  background-color: #2E3440;
  color: #ECEFF4;
}
.dark .nav {
  background-color: #2E3440;
}
.dark .nav .nav__link {
  color: #ECEFF4;
}
.dark .portafolio {
  background-color: #3B4252;
}
.dark .footer {
  background-color: #4C566A;
}